.transaction_history {
  text-align: center;
  width: 800px;
  margin-top: 60px;
  margin-bottom: 60px;
  margin-left: auto;
  margin-right: auto;
  color: rgb(0, 0, 0);
  border: 1px solid rgb(206, 204, 204);    
}

td {
  font-weight: 400;
  font-size: 14px;
  padding: 5px 0 5px 0;
  border: 1px solid rgb(206, 204, 204);
  color: rgb(0, 0, 0);
  cursor: pointer;    
}

th {
  font-size: 18px;
  line-height: 1.3;
  font-weight: 800;
  padding: 10px 0;
  border: 1px solid rgb(205, 206, 204);
  background-color: #0ee9de;
  color: rgb(243, 222, 222);
}